html {
    box-sizing: border-box;
}
*,
*::before,
*::after {
    box-sizing: inherit;
}
a {
    text-decoration: none;
}
li {
    list-style-type: none;
}
p {
    margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}
body {
    height: 100%;
    width: 100%;
    background-color: var(--bg-color);
}
.container {
    max-width: 1200px;
    margin: 0 auto;
}
